import React from 'react'
//mui theme
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
//mui icon
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';

function UserManual(props) {

    return (
        <Dialog fullWidth={true} maxWidth={'sm'} open={props.openModalUserManual} disableEscapeKeyDown={true}>
            <DialogTitle sx={{ fontWeight: 'bolder', fontSize: '20px', textAlign: 'center', color: 'red', textTransform: 'uppercase' }}>Hng dn s dng</DialogTitle>
            <IconButton onClick={() => props.setOpenModalUserManual(false)} sx={{position: 'absolute', right: 5, top: 7}}>
                <CloseIcon fontSize='medium'/>
            </IconButton>
            <DialogContent dividers sx={{pl: '15px', pr: '15px'}}>
                <Typography variant='h6' sx={{color: 'blue', fontWeight: 'bolder'}}>iu dng</Typography>
                    <Box sx={{pl: '5px', pr: '5px'}}>
                        <Typography sx={{color: 'green', fontWeight: 'bolder'}}>ng k khm</Typography>
                        <Typography>Nhn phm F2 hoc nt ng k trn mn hnh</Typography>
                        <Typography sx={{color: 'green', fontWeight: 'bolder'}}>Lm mi form thng tin ng k khm</Typography>
                        <Typography>Nhn phm F4 hoc nt Lm mi trn mn hnh</Typography>
                        <Typography sx={{color: 'green', fontWeight: 'bolder'}}>Tm bnh c</Typography>
                        <Typography>Nhn phm F8 hoc nt Bnh c trn mn hnh, sau khi nhp thng tin v tm c bnh nhn, click p thng tin bnh nhn  form thng tin ng k khm nhn d liu</Typography>
                        <Typography sx={{color: 'green', fontWeight: 'bolder'}}>Sa thng tin bnh nhn</Typography>
                        <Typography>Trong bng danh sch ng k khm trong ngy, nhn vo bnh nhn  m form sa thng tin</Typography>
                    </Box>
                <Typography variant='h6' sx={{color: 'blue', fontWeight: 'bolder'}}>Bc s</Typography>
                    <Box sx={{pl: '5px', pr: '5px'}}>
                        <Typography sx={{color: 'green', fontWeight: 'bolder'}}>Khm bnh</Typography>
                        <Typography>Bc 1: Nhn chn bnh nhn  khm</Typography>
                        <Typography>Bc 2: Nhn F2  bt u khm</Typography>
                        <Typography>Bc 3: Nhn F4  kt thc khm</Typography>
                        <Typography sx={{color: 'green', fontWeight: 'bolder'}}>Sa thng tin hi bnh khi  kt thc khm</Typography>

                    </Box>
                
                <Typography variant='h6' sx={{color: 'blue', fontWeight: 'bolder'}}>Admin</Typography>
                {/* <Typography variant='subtitle1' sx={{color: 'red'}}>Nu c s c lin h zalo: 0987654321 (ch phc v gi hnh chnh)</Typography> */}
            </DialogContent>
        </Dialog>
    )
}

export default UserManual